<template>
    <div id="index" class="container">
        <dv-full-screen-container>
            <div class="login-box">
                <dv-border-box-11 title="登录">
                    <div class="login">
                        <label>请输入密码:</label>
                        <div style="width:240px;display: inline-block;">
                            <dv-border-box-8>
                                <input type="password" v-model="password" placeholder="请输入密码" />
                            </dv-border-box-8>
                        </div>
                    </div>

                    <div class="but">
                        <dv-border-box-10>
                            <div class="w100 h100 flex fff" @click="paswd">登录</div>
                        </dv-border-box-10>
                    </div>
                </dv-border-box-11>
            </div>
        </dv-full-screen-container>
    </div>
</template>
<script>



export default {
    data: function () {
        return {
            password: "",
        };
    },
    mounted() {
        
    },

    methods: {
        paswd() {
            if (!this.password) {
                alert("请输入密码");
            }
            this.$axios({
                // 默认请求方式为get
                method: 'POST',
                url: 'https://xfq.dianfengcms.com/api/screen/login',
                // 传递参数
                data: {
                  password:this.password,
                },
                responseType: 'json'
            }).then(response => {
                let data = response.data;
                if(data.code == 1){
                    alert(data.msg);
                }else{
                    sessionStorage.setItem("Token",data.data.token);
                    this.$router.replace('/');
                }
            })
        }

    },
};
</script>

<style lang="scss" scoped>
.login-box {
    width: 40%;
    margin-left: 30%;
    margin-top: 15%;
    height: 30%;

    & .login {
        text-align: center;
        padding-top: 15%;

        & label {
            color: #fff;
            display: inline-block;
            margin-right: 10px;
        }

        & input {
            width: calc(100% - 30px);
            height: 30px;
            padding: 5px 15px;
            background: none;
            border-radius: 5px;
            border: none;
            color: #fff;
            outline: none;

            &::-webkit-input-placeholder {
                color: rgba(255, 255, 255, 0.685);
            }


        }
    }

    & .but {
        margin: 50px auto 0;
        width: 150px;
        height: 40px;
        border-radius: 5px;
        text-align: center;
    }
}

.w100 {
    width: 100%;
}

.h100 {
    height: 100%;
}

.flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.fff {
    color: #fff;
}
</style>