<template>
  <div class="index-centent">
    <div class="top-box">
      <dv-border-box-11 title="累 计 数 据" :titleWidth="200">
        <div class="top-items">
          <div class="top-item" v-for="(item, index) in toplist" :key="index">
            <div class="number number-font">{{ item.number }}</div>
            <div class="name">{{ item.name }}</div>
          </div>
        </div>
      </dv-border-box-11>
    </div>

    <div class="top-map">
      <dv-border-box-11 title="地 图 展 示" :titleWidth="200">
        <div id="chartViewMap">

        </div>
      </dv-border-box-11>
    </div>

    <div class="bottom-box">
      <dv-border-box-11 title="基 础 数 据" :titleWidth="200">
        <div class="item" v-for="(item, index) in list" :key="index">
          <dv-border-box-7>
            <div class="left">
              <img :src="item.icons" />
            </div>
            <div class="right">
              <div class="top number-font">{{ item.number }}</div>
              <div class="bottom">{{ item.name }}</div>
            </div>
          </dv-border-box-7>
        </div>
      </dv-border-box-11>
    </div>

  </div>
</template>

<script>
export default {
  name: 'indexCentent',
  props: {
    toplist: {
      type: Array,
      default: function () {
        return []
      }
    },
    mapData: {
      type: Array,
      default: function () {
        return []
      }
    },
    list: {
      type: Array,
      default: function () {
        return []
      }
    },
    spotData: {
      type: Array,
      default: function () {
        return []
      }
    },
  },
  data: function () {
    return {
      bgColor: {
        color: [],
        bgColor: ""
      },
    }
  },
  mounted: function () {
    this.echarts_map();
  },
  methods: {
    echarts_map() {
      let areadata = this.mapData;
      var area = [];
      var total = 0;
      for (let i = 0; i < areadata.length; i++) {
        var value = parseInt(areadata[i].value);
        if (total < value) {
          total = value;
        }
        area[i] = areadata[i].name;
      }
      var echarts = require('echarts');
      let that = this;
      let areavalue = this.setCeil(total);
      this.$axios.get('https://xfq.dianfengcms.com/610800_full.json')
        .then(function (chinaJson) {
          chinaJson = chinaJson.data;
          echarts.registerMap('Yulin', chinaJson);
          var mapchart = echarts.init(document.getElementById('chartViewMap'));
          var mapoption = {
            tooltip: {
              show: true,
              trigger: 'item',
              formatter: function (params) {
                if (params.seriesName != "") {
                  return `${params.name}:<br/>核销数：${params.data.value}`;
                }
              },
            },
            title: [{
              left: 'center',
              textStyle: {
                color: '#fff'
              }
            }],
            visualMap: {
              min: 0,
              max: areavalue,
              left: '90%',
              bottom: 10,
              text: ['高', '低'], // 文本，默认为数值文本
              calculable: true,
              color: ['#00169d', '#006be4', '#3eabff'],
              textStyle: {
                color: '#fff'
              },
              show: false,
              formatter: function (value) { //标签的格式化工具。
                return value; // 范围标签显示内容。
              }
            },
            grid: {
              left: 80,
              top: 100,
              bottom: '20%',
              width: '20%'
            },

            xAxis: {
              type: 'value',
              scale: true,
              show: true, //是否显示 x 轴
              position: 'top',
              splitLine: {
                show: false
              },
              axisLine: {//坐标 轴线
                show: true,
                lineStyle: {
                  color: 'rgba(255,255,255,0.8)',
                }
              },
              axisTick: {//坐标轴刻度相关设置
                show: true,
                lineStyle: {
                  color: '#ddd'
                }
              },
              axisLabel: {
                fontSize: 10,
                margin: 5,
                rotate: 45,
                textStyle: {
                  color: '#ddd'
                },
                formatter: function (value) { //标签的格式化工具。
                  return value; // 范围标签显示内容。
                }
              },
            },
            yAxis: {
              type: 'category',
              //name: 'TOP 20',
              nameGap: 10,
              axisLine: {
                show: true,
                lineStyle: {
                  color: '#ccc'
                }
              },
              axisTick: {
                show: false,
                lineStyle: {
                  color: '#ccc'
                }
              },
              axisLabel: {
                interval: 0,
                textStyle: {
                  color: '#ddd'
                }
              },
              data: area
            },
            geo: {
              map: 'Yulin',
              roam: false,
              layoutCenter: ['60%', '50%'], // 地图中心在屏幕中的位置
              layoutSize: '90%', //地图的大小
              label: {
                normal: {
                  show: true,
                  color: '#ffffff'
                },
                emphasis: {
                  color: '#fff'
                }
              },
              itemStyle: {
                normal: {
                  areaColor: "rgba(43, 196, 243, 0.42)",
                  borderColor: "rgba(43, 196, 243, 1)",
                  borderWidth: 1
                },
                emphasis: {
                  areaColor: "#2B91B7"
                }
              },
            },
            series: [
              {
                id: 'bar',
                zlevel: 2,
                type: 'bar',
                symbol: 'none',
                itemStyle: {
                  normal: {
                    color: '#ddb926'
                  }
                },
                data: areadata // 上方定义的数据
              },

            ]

          };
          // 动态显示tootip
          var faultByHourIndex = 0; //播放所在下标
          that.spotData.forEach((item) => {
            item.value.forEach((itm) => {
              const randomHex = () => `#${Math.floor(Math.random() * 0xffffff).toString(16).padEnd(6, "0")}`;
              let obj = {
                type: 'effectScatter',
                coordinateSystem: "geo",
                data: [{
                  name: itm.nickname,
                  value: [itm.longitude, itm.latitude, 2]
                }],
                symbolSize: function (val) {
                  return val[2]
                },
                encode: {
                  value: 2
                },
                rippleEffect: {
                  number: 2,
                  period: 4,
                  scale: 10,
                  brushType: 'stroke',
                  color: randomHex(),
                },
                showEffectOn: 'render',

                effectType: "ripple",
                emphasis: {
                  scale: false
                },
                label: {
                  formatter: '{b}',
                  position: 'right',
                  show: false
                },
                itemStyle: {
                  shadowBlur: 1,
                  shadowColor: 'rgba(230, 10, 10, 1)',
                },
                zlevel: 3
              }
              mapoption.series.push(obj);
            })
          });
          console.log(mapoption);


          setInterval(function () { //使得tootip每隔三秒自动显示

            mapchart.dispatchAction({
              type: 'showTip', // 根据 tooltip 的配置项显示提示框。
              seriesIndex: 0,
              dataIndex: faultByHourIndex
            });
            faultByHourIndex++;
            // faultRateOption.series[0].data.length 是已报名纵坐标数据的长度
            if (faultByHourIndex > mapoption.series[0].data.length) {
              faultByHourIndex = 0;
            }
          }, 4000);
          mapchart.setOption(mapoption);
          window.addEventListener("resize", function () {
            mapchart.resize();
          });
        });

    },
    setCeil(value) {
      if (value < 10) {
        return 10
      } else {
        let num = Number(value.toString().substring(0, 1));
        return (num + 1) * Math.pow(10, (value.toString().length - 1));
      }
    }
  }
}
</script>

