<template>
    <div  class="container videosList">
        <dv-full-screen-container >
            <div class="title-box">
                <div class="tiem">
                    <div>{{ nowDate }}</div>
                </div>
                <div class="title">榆林市文旅消费券发放监管平台</div>
                <div class="button-isFullscreen">
                    <dv-border-box-10>
                        <div @click="fullScreenClick" class="isFullscreen">
                            {{ isFullscreen ? "退出全屏" : "进入全屏" }}
                        </div>
                    </dv-border-box-10>
                </div>
            </div>
            <div class="map">
                <!-- <div class="Tmap" id="map">地图</div> -->
                <div class="left">
                    <index-left :colData="colData" :top_seller_20="top_seller_20" :top_write_off_20="top_write_off_20" v-if="is_left"></index-left>
                </div>
                <div class="content">
                    <index-centent :toplist="toplist" :mapData="mapData" :list="list" :spotData="spotData"
                        v-if="toplist.length != 0 && mapData.length != 0"></index-centent>
                </div>
                <div class="right">
                    <index-right :dataAge="dataAge" :dataSex="dataSex" :dataVali="dataVali" :listTrend="listTrend"
                      :top_coupon_issue_user_20="top_coupon_issue_user_20"  v-if="dataAge.length != 0 && top_coupon_issue_user_20.length!=0">
                    </index-right>
                </div>
            </div>
        </dv-full-screen-container>
    </div>
</template>
<script>
import IndexLeft from '../../components/indexLeft.vue';
import IndexCentent from '../../components/indexCentent.vue';
import IndexRight from '../../components/indexRight.vue';


export default {
    components: { IndexCentent, IndexRight, IndexLeft },
    data: function () {
        return {
            nowDate: "",
            isFullscreen: false,  // 是否为全屏状态
            loading: true,
            Token: '',
            dataAge: [],//年龄统计
            dataSex: [],//性别统计
            dataVali: [],//实名统计
            listTrend: [],//访问量统计
            toplist: [],//累计数据
            mapData: [],//地图数据
            list: [],
            spotData: [],
            colData: [],
            top_coupon_issue_user_20:[],
            top_seller_20:[],
            top_write_off_20:[],
            is_left:false,
        };
    },
    mounted() {
        window.setInterval(() => {
            this.formatDate();
        }, 1000);
        this.init();
        this.initlist();
        window.addEventListener('resize', this.judgeCurrentScreen);
    },
    destroyed() {
        window.removeEventListener('resize', this.judgeCurrentScreen)
    },
    methods: {
        init() {
            this.Token = sessionStorage.getItem("Token");
            if (!this.Token) {
                this.$router.replace('login');
                return false;
            }
            this.$axios({
                // 默认请求方式为get
                method: 'POST',
                url: 'https://wlxfq.dianfengcms.com/api/screen/index',
                // 传递参数
                headers: {
                    Token: this.Token,
                },
                responseType: 'json'
            }).then(response => {
                let data = response.data;
                if (data.code == 0) {
                    this.dataAge = data.data.dataAge;
                    this.dataSex = data.data.dataSex;
                    this.dataVali = data.data.dataVali;
                    this.listTrend = data.data.listTrend;
                    this.toplist = data.data.toplist;
                    this.mapData = data.data.mapData;
                    data.data.list.map((item,index) => {
                        this.$set(item, 'icons', require(`../../assets/icons/${index + 1}.png`));
                        return item;
                    });
                    this.list = data.data.list;
                    this.spotData = data.data.spotData;
                    this.colData = data.data.colData;


                } else {
                    alert(data.msg);
                    this.$router.replace('login');
                }
            })
        },
        initlist() {
            this.$axios({
                // 默认请求方式为get
                method: 'POST',
                url: 'https://wlxfq.dianfengcms.com/api/screen/list',
                // 传递参数
                headers: {
                    Token: this.Token,
                },
                responseType: 'json'
            }).then(response => {
                let data = response.data;
                this.top_coupon_issue_user_20 = data.data.top_coupon_issue_user_20;
                this.top_seller_20 = data.data.top_seller_20 //热门商家。
                this.top_write_off_20 = data.data.top_write_off_20;
                this.is_left = true;
            })
        },
        formatDate() {
            let date = new Date();
            let year = date.getFullYear(); // 年
            let month = date.getMonth() + 1; // 月
            let day = date.getDate(); // 日
            let week = date.getDay(); // 星期
            let weekArr = [
                "星期日",
                "星期一",
                "星期二",
                "星期三",
                "星期四",
                "星期五",
                "星期六",
            ];
            let hour = date.getHours(); // 时
            hour = hour < 10 ? "0" + hour : hour; // 如果只有一位，则前面补零
            let minute = date.getMinutes(); // 分
            minute = minute < 10 ? "0" + minute : minute; // 如果只有一位，则前面补零
            let second = date.getSeconds(); // 秒
            second = second < 10 ? "0" + second : second; // 如果只有一位，则前面补零
            this.nowDate = `${year}-${month}-${day} ${hour}:${minute}:${second} ${weekArr[week]}`;
        },
        fullScreenClick() {
            let dom = this.$el.querySelector('.videosList')
            if (!this.isFullscreen) {
                this.$fullscreen.enter(dom, {
                    wrap: false,
                    callback: f => {
                        this.isFullscreen = f
                    }
                })
            } else {
                this.$fullscreen.exit(dom, {
                    wrap: false,
                    callback: f => {
                        this.isFullscreen = f
                    }
                })
            }
        },
        checkFull() {
            //判断浏览器是否处于全屏状态 （需要考虑兼容问题）
            //火狐浏览器
            var isFull = document.mozFullScreen ||
                document.fullScreen ||
                //谷歌浏览器及Webkit内核浏览器
                document.webkitIsFullScreen ||
                document.webkitRequestFullScreen ||
                document.mozRequestFullScreen ||
                document.msFullscreenEnabled
            if (isFull === undefined) {
                isFull = false
            }
            console.log(isFull);
            return isFull;
        },
        judgeCurrentScreen() {
            // 非全屏状态
            this.checkFull();
            if (!this.checkFull()) {
                this.isFullscreen = false;
            }
            // 全屏状态
            if (this.checkFull()) {
                this.isFullscreen = true
            }
        }

    },
};
</script>

<style lang="scss" scoped>
.title-box {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: calc(8% - 8px);
    background-image: url('../../../public/title.png');
    background-repeat: no-repeat;
    background-size: 100% auto;
    background-color: #010e50;
    box-shadow:
        25.2px 26.2px 7.4px -28px rgba(0, 0, 0, 0.1),
        42.8px 44.6px 25px -28px rgba(0, 0, 0, 0.155),
        99px 103px 112px -28px rgba(0, 0, 0, 0.25);

    & .tiem,
    & .title {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    & .title {
        width: 40%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 34px;
        font-weight: bold;
        color: #fff;
        letter-spacing: 3px;
        margin-bottom: 20px;
    }

    & .tiem {
        width: 30%;
        display: flex;
        flex-direction: column;
        margin-bottom: 15px;
        color: #fff;

        & div {
            margin-bottom: 2px;
        }
    }

    & .button-isFullscreen {
        width: 30%;
        margin-bottom: 15px;
        width: calc(30% - 150px);
        margin-left: 150px;
        color: #fff;

        .dv-border-box-10 {
            max-width: 25%;
        }

        & .isFullscreen {
            width: calc(100% - 20px);
            text-align: center;
            padding: 8px;
        }
    }
}

.map {
    width: 100%;
    height: 91.5%;
    display: flex;
    justify-content: space-between;
    position: relative;

    & .Tmap {
        position: absolute;
        width: 100%;
        height: 100%;
    }

    & .left,
    & .right {
        width: calc(25% - 10px);
        z-index: 9;
    }

    & .content {
        width: 50%;
        height: 100%;
        z-index: 9;
        position: absolute;
        bottom: 0;
        left: 25%;
    }


}
</style>