import Vue from 'vue'
import Router from "vue-router"
import index from "../pages/Index/Index.vue"
import login from "../pages/Login/Login.vue"
Vue.use(Router);
export default new Router({
    routes: [
        {
            path: '/',
            name: "index",
            component: index,
        },
        {
            path: '/login',
            name: "login",
            component: login,
        },
       
    ]
})
