<template>
  <div class="indexLeft">
    <div class="top">
      <dv-border-box-11 title="商 家 核 销 排 名" :titleWidth="200">

        <div class="top-item">
          <dv-scroll-board :config="configTable" style="width:100%;height:100%" />

        </div>

      </dv-border-box-11>
    </div>
    <div class="centent">
      <dv-border-box-11 title="入 驻 商 家 数 量" :titleWidth="200">
        <div class="content-item" id="number"></div>
      </dv-border-box-11>
    </div>
    <div class="bottom">
      <dv-border-box-11 title=" 商 家 最 新 核 销" :titleWidth="200">
        <div class="bottom-item">
          <div class="nav-items">
            <div class="nav-item" v-for="(item, index) in list" :key="index" :class="[current == index ? 'active' : '']"
              @click="listClick(index)">
              {{ item }}</div>
          </div>
          <dv-scroll-board :config="configTable1" style="width:100%;height:calc(100% - 35px)" ref="scrollBoard1" />
        </div>
      </dv-border-box-11>
    </div>
  </div>
</template>
<script>
export default {
  name: 'indexLeft',
  props: {
    colData: {
      type: Array,
      default: function () {
        return []
      }
    },
    top_seller_20: {
      type: Array,
      default: function () {
        return []
      }
    },
    top_write_off_20: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  data: function () {
    return {
      list: [],
      current: 0,
      configTable: {
        header: ['排名', '所属地区', '商家名称'],
        data: [

        ],
        rowNum: 5,
        align: ['center', 'center', 'center'],
        columnWidth: [65, 100],
        headerBGC: "#020e4d",
        oddRowBGC: "#081c88",
        evenRowBGC: "#021061"

      },
      configTable1: {
        //商家最新核销
        header: ['序号', '消费券名称', '消费券金额', '领取人'],
        data: [

        ],
        rowNum: 4,
        align: ['center', 'center', 'center', 'center'],
        columnWidth: [65, 150],
        headerBGC: "#020e4d",
        oddRowBGC: "#05127c",
        evenRowBGC: "#000e6d"
      }
    }
  },
  mounted() {
    this.echarts_2();
    this.configTable.data = this.top_seller_20.map((item, index) => {
      return [index + 1, item.area, item.nickname];
    });


    this.top_write_off_20.forEach((item) => {
      this.list.push(item.title);
      item.list = item.list.map((ite) => {
        let list = [ite.id, ite.coupon_title, ite.coupon_price, ite.nickname,]
        return list
      })
    })
    this.$set(this.configTable1, 'data', this.top_write_off_20[this.current].list);
    let len = this.list.length;
    let index = 0;
    setInterval(() => {
      if (index < len - 1) {
        index++;
        this.listClick(index);
      } else {
        index = 0;
        this.listClick(index);
      }
    }, 15000)
  },
  methods: {
    listClick(e) {
      this.current = e;
      this.$set(this.configTable1, 'data', []);
      this.configTable1 = {};
      let obj = {
        //商家最新核销
        header: ['序号', '消费券名称', '消费券金额', '领取人'],
        data: this.top_write_off_20[e].list,
        rowNum: 4,
        align: ['center', 'center', 'center', 'center'],
        columnWidth: [65, 150],
        headerBGC: "#020e4d",
        oddRowBGC: "#05127c",
        evenRowBGC: "#000e6d"
      }
      this.configTable1 = obj;
    },
    echarts_2() {
      let area = [], array1 = [];
      this.colData.map((item) => {
        area.push(item.area);
        array1.push(item.value);
      })
      // 基于准备好的dom，初始化echarts实例
      var echarts = require('echarts');
      var myChart = echarts.init(document.getElementById('number'));
      let option = {
        //  backgroundColor: '#00265f',
        tooltip: {
          trigger: 'axis',
          axisPointer: { type: 'shadow' }
        },
        grid: {
          left: '0%',
          top: '10px',
          right: '0%',
          bottom: '4%',
          containLabel: true
        },
        xAxis: [{
          type: 'category',
          data: area,
          axisLine: {
            show: true,
            lineStyle: {
              color: "rgba(255,255,255,.1)",
              width: 1,
              type: "solid"
            },
          },

          axisTick: {
            show: false,
          },
          axisLabel: {
            interval: 0,
            // rotate:50,
            show: true,
            splitNumber: 15,
            rotate: 38,
            textStyle: {
              color: "rgba(255,255,255,.6)",
              fontSize: '10',
            },
          },
        }],
        yAxis: [{
          type: 'value',
          axisLabel: {
            //formatter: '{value} %'
            show: true,
            textStyle: {
              color: "rgba(255,255,255,.6)",
              fontSize: '10',
            },
          },
          axisTick: {
            show: false,
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: "rgba(255,255,255,.1	)",
              width: 1,
              type: "solid"
            },
          },
          splitLine: {
            lineStyle: {
              color: "rgba(255,255,255,.1)",
            }
          }
        }],
        series: [
          {

            type: 'bar',
            data: array1,
            barWidth: '35%', //柱子宽度
            // barGap: 1, //柱子之间间距
            itemStyle: {
              normal: {
                color: '#27d08a',
                opacity: 1,
                barBorderRadius: 5,
              }
            }
          }

        ]
      };

      var faultByHourIndex = 0; //播放所在下标

      setInterval(function () { //使得tootip每隔三秒自动显示
        myChart.dispatchAction({
          type: 'showTip', // 根据 tooltip 的配置项显示提示框。
          seriesIndex: 0,
          dataIndex: faultByHourIndex
        });
        faultByHourIndex++;
        // faultRateOption.series[0].data.length 是已报名纵坐标数据的长度
        if (faultByHourIndex > option.series[0].data.length) {
          faultByHourIndex = 0;
        }
      }, 4000);
      // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(option);
      window.addEventListener("resize", function () {
        myChart.resize();
      });
    }
  },

}
</script>


